import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/components/Home';

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/snippet/:id',
      name: 'Snippet',
      component: () => import('@/components/Snippet.vue')
    }
  ]
});